import {Link} from "react-router-dom";
import logo from "./img/cahaba_logo.jpg";
import ew_logo from "./img/ew_logo.jpg";

import "./DefaultLayout.css";

function DefaultLayout(props) {


  const links = [
    {to: "/contact", name: "Contact"},
    {to: "/athletes", name: "Athletes"},
    {to: "/races", name: "Races"}
  ]

  const sponsors = [
    {img: ew_logo, link: "https://ewmotiontherapy.com/"}
  ];

  return (
      <div className="DefaultLayout vertical-flex-container"  >
        <header className="site-header horizontal-flex-container" >
          <div className="cahaba-logo">
            <Link to="/">cahaba</Link>
          </div>

          <div className="vertical-end-flex-container">
            <ul className="horizontal-flex-container menu-container">
              {
                links.map(function(l) {
                  return (
                    <li className="menu-box">
                      <Link className="black-white-link" to={l.to}>
                        {l.name}
                      </Link>
                    </li>
                  );
                })
              }
            </ul>
          </div>

        </header>

        <div className="page-content vertical-flex-container body-text">
          {props.children}
        </div>

        <footer className="site-footer horizontal-flex-container black-on-blue">
              
              <div className="vertical-center-flex-container">
                <h4 className="sponsor-header">Sponsors</h4>
              </div>

                {sponsors.map(sponsor => {
                  return (
                    <div className="sponsor-box">
                      <a href={sponsor.link} target="_blank">
                        <img className="sponsor-image" src={sponsor.img}/>
                      </a>
                    </div>
                  );
                })}

        </footer>

      </div>
  );
}

export default DefaultLayout;
