import DefaultLayout from "./DefaultLayout";

import races_json from "./races.json";

function Races(props) {
    const races = races_json.races;
    const toRow = function(race) {
        var cells = [];
        cells.push( <td>{race.date}</td> );

        if (race.link)
            cells.push( <td><a href={race.link} target="_blank">{race.name}</a></td>)
        else
            cells.push( <td>{race.name}</td>);

        return (<tr>{cells}</tr>);
    };

    const rows = races.map(toRow);

    return (
        <DefaultLayout>
            <h1 className="page-header">Races</h1>
            <table>
                <tr>
                    <th>Date</th>
                    <th>Race</th>
                </tr>
                {rows}
            </table>
        </DefaultLayout>
    )

}

export default Races;